// Libraries
import React, { useState, useEffect } from 'react'
import { Button, Input, Segment, Header, Divider, Message } from 'semantic-ui-react'
import axios from 'axios'
// Custom hooks
import { useNotification } from '../hooks/index'

const SensorUrlForm = () => {
  const [sensorsConnectedUrl, setSensorsConnectedUrl] = useState(null)
  const [sensorUrlField, setSensorUrlField] = useState('')
  const [notification, setNotification] = useNotification()

  const handleConnect = async () => {
    console.log('sensorUrlField', sensorUrlField)
    try {
      const response = await axios.get(sensorUrlField)
      console.log('response', response)

      if (Object.keys(response.data).includes('sensors')) {
        window.confirm('Would you like to receive information about these sensors?')
          ? setSensorsConnectedUrl(sensorUrlField)
          : setSensorsConnectedUrl(null)
      }
    } catch (error) {
      setNotification(error.message)
    }


  }

  return (
    <Segment>
      <Header>
        Sensor resource URL
       </Header>
      <small style={{ color: 'gray' }}>
        Provide an endpoint URL where sensor output comes from
       </small>
      <Input
        value={sensorUrlField}
        onChange={({ target }) => setSensorUrlField(target.value)}
        fluid
        placeholder='Enter URL...'
        action={
          <Button loading={false} onClick={handleConnect}>connect</Button>
        }
      />
      {
        sensorsConnectedUrl
          ?
          <Segment color={'green'}>
            connected at: <br />
            {sensorsConnectedUrl}
          </Segment>
          :
          <Segment color={'red'}>
            not connected
          </Segment>
      }
      <Divider />
      {notification &&
        <Message color='red'>
          {notification}
        </Message>
      }
    </Segment>
  )
}

export default SensorUrlForm